ent: center;\n  align-items: center;\n\n  gap: 1rem;\n}\n\n@mixin ellipsis {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n  word-wrap: normal;\n}\n"],"sourceRoot":""}]);
