 them in CSS also allows us to read the variables in JS as well as CSS and SCSS\n\n// Theme colors\n$background: var(--background);\n$text-color: var(--text-color);\n\n$primary: var(--primary);\n$secondary: var(--secondary);\n\n$focus: var(--focus);\n\n// Non color CSS variables\n$border-radius: 3px;\n\n$box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);\n\n$font-family: 'Open Sans', 'Helvetica', 'Arial', sans-serif;\n\n// Responsive Screen Sizes\n$x-large: 1500px;\n$large: 1200px;\n$medium: 1000px;\n$small: 600px;\n$x-small: 320px;\n\n@mixin response-x-large {\n  @media (max-width: $x-large) {\n    @content;\n  }\n}\n\n@mixin response-large {\n  @media (max-width: $large) {\n    @content;\n  }\n}\n\n@mixin response-medium {\n  @media (max-width: $medium) {\n    @content;\n  }\n}\n\n@mixin response-small {\n  @media (max-width: $small) {\n    @content;\n  }\n}\n\n@mixin response-x-small {\n  @media (max-width: $x-small) {\n    @content;\n  }\n}\n\n@mixin pop-up {\n  transition: top 0.1s linear;\n  position: relative;\n  top: 0;\n\n  &:hover {\n    top: -0.25rem;\n  }\n}\n\n@mixin flex-center {\n  display: flex;\n\n  justify-content: center;\n  align-items: center;\n\n  gap: 1rem;\n}\n\n@mixin ellipsis {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n  word-wrap: normal;\n}\n"],"sourceRoot":""}]);
